<template>
  <div>
    <CompanyCard :title="title" :companies_data="profile_companies" />
  </div>
</template>
<script>
import instance from "../../store/axiosConfig.js";
import CompanyCard from "../components/CompanyCard";
export default {
  name: "PackageProfile",
  data() {
    return {
      title: "Profile Package",
      profile_companies: null,
    };
  },
  components: {
    CompanyCard,
  },
  methods: {
    getCompanies(id) {
      instance
        .get("/frontend/companies/" + id)
        .then((res) => {
          console.log(res.data);
          this.profile_companies = res.data.data;
        })
        .catch((err) => {
          console.log(err.message);
        });
    },
  },
  mounted() {
    this.getCompanies(2);
  },
};
</script>
